import { Component, OnInit, ViewChild } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { Router } from '@angular/router';
import { FirebaseApp } from '@angular/fire';
import { AngularFireAuthModule } from 'angularfire2/auth';
import * as firebase from 'firebase/app';


@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage {
  @ViewChild('username') uname;
  @ViewChild('password') password;

constructor(private fire: AngularFireAuth, private router: Router){}

  signIn() {
    let rgx = new RegExp("@");
    if(rgx.exec(this.uname.value) != null) {
      this.fire.auth.signInWithEmailAndPassword(this.uname.value, this.password.value).then(data =>
        this.router.navigate(['profile']));
    } else {
    this.fire.auth.signInWithEmailAndPassword(this.uname.value + '@domain.xta', this.password.value).then(data =>
      this.router.navigate(['profile']));
    } 
  }
  signUp() {
    this.router.navigate(['register'])
  }
  googleSignIn = () => {
    this.fire.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider()).then(data => {
      this.router.navigate(['profile']);
    })
  }

}
